import { Component, OnInit } from '@angular/core';
import { Http } from '@angular/http';
import { UserService } from '../home/user/userService.service';
import { Response } from '@angular/http';
import { NgForm } from '@angular/forms';


@Component({
  selector: 'app-histroy',
  templateUrl: './histroy.component.html',
  styleUrls: ['./histroy.component.scss']
})
export class HistroyComponent implements OnInit {



  usersData = [];
  userinfo;
  constructor(private http: Http, private userService: UserService) { }

  ngOnInit() {
    this.userService.getData()
      .subscribe(
      (data: Response) => {
        console.log(data);
        const temp = data.json();
        for (const elm in temp) {
          if (temp.hasOwnProperty(elm)) {
            this.usersData.push(temp[elm]);
          }
        }
      }
      );
  }
  onSearch(form: NgForm) {
    for (const data of this.usersData) {

      if (form.value.search === data.login.username || form.value.search === data.name.first ||
        form.value.search === data.name.last || form.value.search === data.gender || form.value.search === data.email
        || form.value.search === data.phone) {
        this.usersData.splice(0);
        this.usersData.push(data);
        form.reset();
      }
    }
  }
  onAll() {
    this.usersData.splice(0);
    this.userService.getData()
      .subscribe(
      (data: Response) => {
        const temp = data.json();
        for (const elm in temp) {
          if (temp.hasOwnProperty(elm)) {
            this.usersData.push(temp[elm]);
          }
        }
      }
      );
  }

}



